@font-face {
  font-family: Quicksand;
  src: url(f9baef8ac0d836e6486419e282e42336.ttf);
}
@font-face {
  font-family: Quicksand;
  src: url(3cd49fcca0fcaa042649d15bab54199e.ttf);
  font-weight: bold;
}
@font-face {
  font-family: Montserrat;
  src: url(ee6539921d713482b8ccd4d0d23961bb.ttf);
}
@font-face {
  font-family: Montserrat;
  src: url(ade91f473255991f410f61857696434b.ttf);
  font-weight: bold;
}
* {
  padding: 0;
  margin: 0;
}
body {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
}
h1 {
  font-family: 'Quicksand', Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  color: #1F2041;
}
h2 {
  font-family: 'Quicksand', Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;
  color: #1F2041;
}
h3 {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1F2041;
}
